import React from 'react';
import { jsonFormatter } from '../utilities/jsonFormatter';
import swal from 'sweetalert2';

const Footer = ({ jsonContent, formattedJson, setFormattedJson }) => {
  const handleButtonClick = () => {
    if (jsonContent !== '') {
      let formattedData = jsonFormatter(jsonContent);
      setFormattedJson({ labels: formattedData[1], datasets: formattedData[0] });
    } else {
      swal.fire({
        title: 'Invalid data!',
        text: 'Please, insert all kinds of data required!',
        icon: 'error'
      });
    }
    // return console.log('Pressed', { labels: formattedData[1], datasets: formattedData[0] });
  };

  return (
    <div className="footer">
      <button onClick={() => handleButtonClick()}>Generate Chart</button>
    </div>
  );
};

export default Footer;
